/**
 * @module components\home
*/
import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from 'vue-property-decorator';
@Component({
 
  })
export default class Home extends Vue {
  irctcIdReq = {"amount":"600", "moduleName":"IRCTC_INTRO_TRAVAL_UNION"};
  callUsReq = {"action":"DIALER", "content":"01205077784"};
  emailReq = {"emailId":"care@travelunion.in", "subject":"IRCTC OTP base booking","message":"IRCTC OTP base booking","title":"IRCTC OTP base booking"};
  buyIrctc (){
    window.parent.postMessage(JSON.stringify({methodName:"proceedToPayment",data:this.irctcIdReq}),"*");
     flutter_inappwebview.callHandler("proceedToPayment",JSON.stringify(this.irctcIdReq));
  }
  callUs(){
    window.parent.postMessage(JSON.stringify({methodName:"openWebOrDialer",data:this.callUsReq}),"*");
    flutter_inappwebview.callHandler("openWebOrDialer",JSON.stringify(this.callUsReq));
  }
  mailUs(){
    window.parent.postMessage(JSON.stringify({methodName:"openEmail",data:this.emailReq}),"*");
    flutter_inappwebview.callHandler("openEmail",JSON.stringify(this.emailReq));
  }
  close(){
    window.parent.postMessage(JSON.stringify({methodName:"closeSession",data:null}),"*");
    flutter_inappwebview.callHandler("closeSession");
  }
}